.site {
    &-title {
        float: left;
        padding: .5em 0;

        > h1 {
            font-size:1.5em;
            font-weight: normal;
            margin: .5em 0 .25em 0;
            text-transform: capitalize;

            > a {
                color: $header-text-color;
            }
        }
        > .ext-location {
            > a {
                color: $header-text-color;
            }
        }
    }
    &-utilityBar {
        float: right;
        margin: 0 0 0 1em;
        position: relative;
        z-index: 7;

        > .ext-launcher {
            cursor: pointer;
            display: block;
        }
        > .ext-links {
            background: #fff;
            border: 1px solid #f3f3f3;
            box-shadow: 0 0 1em 0 rgba(0, 0, 0, .5);
            opacity: 0;
            padding: .5em 0;
            position: absolute;
                top: 1.5em;
                right: 0;
                left: inherit;
            transition: opacity .25s;
            width: 12em;

            &.open {
                opacity: 1;
            }
            
            &.closed {
                top: -10000px;
                left: -10000px;
            }
                
            a {
                display: block;
                padding: .5em 1em;
                margin: 0;
                transition: background .25s, color .25s;

                &:hover {
                    background: $header-text-color;
                    color: #fff;
                }
            }
        }
    }
}
